 * @LastEditTime: 2019-08-27 16:29:25
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="notice_wrapper">
    <Header></Header>
    <nav class="nav_wrapper">
      <div class="search_wrapper">
        <p class="search_title">嗨！有什么需要帮忙的吗？</p>
        <p class="search_input">
          <el-input placeholder="搜索" @change="search(searchVal)" v-model="searchVal">
            <i slot="prefix" class="el-input__icon el-icon-search"></i>
          </el-input>
        </p>
      </div>
    </nav>
    <div class="notice_content">
      <div class="notice_left_title">
        <p class="notice_main_title">问题分类</p>
        <p class="notice_subhead" @click="()=>{activeAnchor = index}" :class="activeAnchor == index?'notice_subhead_active':''" v-for="(item,index) in anchorList" :key="index" v-anchor="index+1">{{item.name}}</p>
      </div>
      <div class="notice_right_content">
        <el-row class="coin_trade_wrapper" id="anchor-1">
          <el-col class="coin_trade_title" >币币交易</el-col>
          <el-col class="coin_trade_content" @click.native="goTo('币币交易')">阶梯手续费率和HT抵扣手续费-操作指南（网页端）</el-col>
          <el-col class="coin_trade_content">关于BTC合约实时结算功能上线的公告 </el-col>
          <el-col class="coin_trade_content">阶梯手续费率和HT抵扣手续费-操作指南（网页端）</el-col>
          <el-col class="coin_trade_content">关于BTC合约实时结算功能上线的公告</el-col>
          <el-col class="look_more">查看更多</el-col>
        </el-row>
        <el-row class="coin_trade_wrapper" id="anchor-2">
          <el-col class="coin_trade_title">最新活动</el-col>
          <el-col class="coin_trade_content" @click.native="goTo('最新活动')">阶梯手续费率和HT抵扣手续费-操作指南（网页端）<span class="time fr">2019-08-12 14:54</span></el-col>
          <el-col class="coin_trade_content">关于BTC合约实时结算功能上线的公告 <span class="time fr">2019-08-12 14:54</span></el-col>
          <el-col class="coin_trade_content">阶梯手续费率和HT抵扣手续费-操作指南（网页端）<span class="time fr">2019-08-12 14:54</span></el-col>
          <el-col class="coin_trade_content">关于BTC合约实时结算功能上线的公告<span class="time fr">2019-08-12 14:54</span></el-col>
          <el-col class="look_more" >查看更多</el-col>
        </el-row>
        <el-row class="coin_trade_wrapper" id="anchor-3">
          <el-col class="coin_trade_title">公告中心</el-col>
          <el-col class="coin_trade_content" @click.native="goTo('公告中心')">阶梯手续费率和HT抵扣手续费-操作指南（网页端）</el-col>
          <el-col class="coin_trade_content">关于BTC合约实时结算功能上线的公告 </el-col>
          <el-col class="coin_trade_content">阶梯手续费率和HT抵扣手续费-操作指南（网页端）</el-col>
          <el-col class="coin_trade_content">关于BTC合约实时结算功能上线的公告</el-col>
          <el-col class="look_more" >查看更多</el-col>
        </el-row>
      </div>

    </div>
  </div>
</template>

<script>
  import Header from '@/components/header/header'

  export default {
   data() {
    return {
      anchorList:[{name:'币币交易'},{name:'最新活动'},{name:'公告中心'}],
      activeAnchor:'0',
      searchVal:''
    }
   },
   created(){
   },
   methods: {
    /*跳转页面*/
    goTo(str){
      // console.log('this.$route', this.$route.meta.bread)
      localStorage.setItem('breads',JSON.stringify(this.$route.meta.bread))
      this.$router.push({path:'/noticeDetail',query:{name:str}})
    },

    /* 搜索 */
    search(){
      localStorage.setItem('breads',JSON.stringify(this.$route.meta.bread))
      this.$router.push({path:'/searchNotice',query:{name:'搜索结果'}})
    },

    /* 锚点跳转 */
    jump(id){
      var anchor = this.$el.querySelector("#" + id)
      document.documentElement.scrollTop = anchor.offsetTop
    }
   },
   components: {
     Header
   }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/style/base.scss';

  .notice_wrapper{
    width: 100%;
    min-height: 100%;
    background-color: $light_font_color;
    min-width: 1280px;
    padding-top: 44px;
    padding-bottom: 15px;
    box-sizing: border-box;
    .nav_wrapper{
      width: 100%;
      height: 300px;
      background:url('./notice_banner.jpg') no-repeat;
      background-size: 100% 100%;
      position: relative;
      .search_wrapper{
        width: 400px;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        -o-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        .search_title{
          line-height: 30px;
          height: 30px;
          color: $light_font_color;
          font-size: $font24;
          text-align: center;
          margin-bottom: 10px;
        }
      }
    }
    .notice_content{
      width: 1200px;
      margin: 0 auto;
      padding: 8px 0 60px 200px; 
      box-sizing: border-box;
      background-color:$light_font_color;
      position: relative;
      .notice_left_title{
        position: absolute;
        left: 0;
        top: 25px;
        .notice_main_title{
          font-size: $font18;
          margin-bottom: 15px;
          color: $noraml_font_color;
          font-weight: 600;
        }
        .notice_subhead{
          font-size: $font16;
          margin-bottom: 10px;
          color: $dark_title_color;
          font-weight: 600;
          cursor: pointer;
          &:hover{
            color: $noraml_font_color;
          }
          &:active{
            color: $noraml_font_color;
          }
        }
        .notice_subhead_active{
          color: $noraml_font_color;
        }
      }
      .notice_right_content{
        width: 100%;
        padding-bottom: 100px;
        .coin_trade_wrapper{
          margin-bottom: 70px;
        }
        .coin_trade_title{
          font-size: $font18;
          color: $noraml_font_color;
          font-weight: 600;
          line-height: 60px;
          border-bottom: 1px solid $border_dark_color;
        }
        .coin_trade_content{
          line-height: 60px;
          font-size: $font18;
          color: $dark_title_color;
          border-bottom: 1px solid $border_dark_color;
          cursor: pointer;
          &:hover{
            text-decoration:underline
          }
        }
        .look_more{
          margin-top: 15px;
          cursor: pointer;
          &:hover{
            color: $active_font_color;
          }
        }
        .time{
          font-size: $font14;
          color: $dark_font_color;
        }

      }
     
    }
  }
</style>



